
















import { Component, Prop, Watch, Vue } from 'vue-property-decorator';
import { mapState } from 'vuex';
import VueYouTubeEmbed from 'vue-youtube-embed';
import 'velocity-animate';

Vue.use(VueYouTubeEmbed);

/**
 * YouTubeã®ãããªãã¬ã¤ã¤ã¼ã®ã³ã³ãã¼ãã³ã
 * [ãã©ã°ã¤ã³]
 * VueYouTubeEmbedï¼YouTubeã®åãè¾¼ã¿ãã¬ã¤ã¤ã¼ãåçã«æ±ããã©ã°ã¤ã³
 *
 */
@Component({
  computed: mapState([
    'currentVideoIndex',
    'currentVideoId'
  ])
})
export default class YouTubeVideoPlayer extends Vue {
  /* YouTubeã®ãããªID */
  public videoId: string = '';

  // åãè¾¼ã¿ãã¬ã¤ã¤ã¼ã®ãµã¤ãº
  public width: number = 0;
  public height: number = 0;
  // åãè¾¼ã¿ãã¬ã¤ã¤ã¼ã®åçãªãã·ã§ã³
  public videoOptions: any = {
    rel: 0,
    showinfo: 0
  };

  /* è¡¨ç¤ºãã©ã° */
  public isShown: boolean = true;
  /* è¡¨ç¤ºã»éè¡¨ç¤ºã§ã¹ã©ã¤ããããæ¹å */
  protected slideDirection: string = 'left';

  /* mapStateç¨ãã­ããã£ */
  currentVideoIndex: number;
  currentVideoId: string;


  /* /////////////////////////////////////////
  ** Watcher
  //////////////////////////////////////////// */
  /**
   * ä¸ã®ytVideoIdãã­ããã£ã®ã¦ã©ããã£ã¼
   * æ´æ°æã«éè¡¨ç¤ºâè¡¨ç¤ºãã©ã³ã¸ã·ã§ã³ãå®è¡ããããã«watchãã
   */
  @Watch('currentVideoIndex')
  protected onYtVideoIdChange(newValue: string, oldValue: string): void {
    // this.videoId = newValue;
    if (newValue !== oldValue) {
      if (newValue > oldValue) {
        // æ¬¡ã®ãããªãå³ããå·¦ã«åºã
        this.slideDirection = 'left';
      }
      else {
        // æ¬¡ã®ãããªãå·¦ããå³ã«åºã
        this.slideDirection = 'right';
      }
      this.isShown = false;
    }
  }


  /* /////////////////////////////////////////
  ** LifeCycles
  //////////////////////////////////////////// */
  /* DOMãã¦ã³ãåã«å®è¡ãããã¡ã½ãã */
  protected beforeMount(): void {
    // ååèµ·åæã¯ytVideoIdãã­ããã£ãvideoIdã«ã»ãããã
    // ã¤ã¾ãããã¬ã¤ã¤ã¼ã®video-idãã¤ã³ãã£ã³ã°ã«æ¸¡ã
    this.videoId = this.currentVideoId;
  }


  /* /////////////////////////////////////////
  ** è¡¨ç¤ºã»éè¡¨ç¤ºã¡ã½ããç¾¤ï¼ã¢ãã¡ã¼ã·ã§ã³ï¼
  //////////////////////////////////////////// */
  /* è¦ç´ éç½®å */
  protected beforeEnter(el: HTMLElement): void {
    console.log('Before Enter');
    // éæåº¦ã0ã«
    el.style.opacity = '0';
    // åæXä½ç½®ãè¨­å®
    const initTransX: string = this.slideDirection === 'left' ? '20%' : '-20%';
    el.style.transform = 'translateX(' + initTransX + ')';
  }
  /* è¦ç´ éç½®ï¼è¡¨ç¤ºã¢ãã¡ã¼ã·ã§ã³å®è¡ï¼ */
  protected enter(el: HTMLElement): void {
    console.log('Enter Animation');
    const initTransX: string = this.slideDirection === 'left' ? '20%' : '-20%';

    Velocity.animate(el, {
      opacity: [1.0, 0],
      translateX: ['0%', initTransX]
    }, {
      duration: 400,
      easing: 'easeOutQuart'
    });
  }
  /* è¦ç´ éè¡¨ç¤ºï¼éè¡¨ç¤ºã¢ãã¡ã¼ã·ã§ã³å®è¡ï¼ */
  protected leave(el: HTMLElement, done: () => void): void {
    console.log('Leave Animation');
    const hideTransX: string = this.slideDirection === 'left' ? '-20%' : '20%';
    Velocity.animate(el, {
      opacity: [0, 1.0],
      translateX: [hideTransX, '0%']
    }, {
      duration: 300,
      easing: 'easeOutQuart',
      complete: (target) => {
        console.log(target);
        done();
      }
    });
  }
  /* è¦ç´ éè¡¨ç¤ºæã¯ãããªIDãã»ãããã¦åè¡¨ç¤º */
  protected afterLeave(el: HTMLElement): void {
    console.log('After Leave');
    this.videoId = this.currentVideoId;
    this.isShown = true;
  }


  /* /////////////////////////////////////////
  ** Methods
  //////////////////////////////////////////// */
  /**
   * ãããªãã¬ã¤ã¤ã¼ã®ãµã¤ãºãã¢ãããã¼ããã
   * @param width ãã¬ã¤ã¤ã¼ã®å¹
   * @param height ãã¬ã¤ã¤ã¼ã®é«ã
   */
  public updateSize(width: number, height: number): void {
    this.width = width;
    this.height = height;
    // ã«ã¼ãè¦ç´ ã®.video-boxã®ãµã¤ãºè¨­å®
    this.$el.style.width = this.width + 'px';
  }
}
